/* top panel */
.topPanelContainer {
  margin-left: 62px;
  margin-top: 80px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 159px;
  gap: 61px;
  margin-right: 59px;
}

.topPanelContainer .topPanelRight img {
  height: 85vh;
  width: 100vw;
  object-fit: fill;
  position: relative;

  @media screen and (min-width: 1440px) {
    height: 90vh;
  }
}

// .topPanelContainer .topPanelRight {
//   flex: 55%;
// }
.flex {
  display: flex;
  align-items: center;
}

.topPanelContainer .topPanelRight .topPanelCard {
  max-width: 35vw;
  background-color: rgba(255, 255, 255, 0.98);
  z-index: 99;
  height: fit-content;
  position: absolute;
  top: 400px;
  left: 369px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  box-shadow: 10px 10px 40px lightgray;
}

.topPanelContainer .topPanelRight .topPanelCard h2 {
  color: rgba(118, 120, 114, 1);
  line-height: 21px;
  font-size: 14px;
  margin: 5px 10px;
}

.topPanelContainer .topPanelRight .topPanelCard img {
  width: 38px;
  height: 38px;
  margin: 10px 10px;
}

.topPanelContainer .topPanelRight .card-bottom {
  width: 25vw;
  background-color: rgba(255, 255, 255, 0.98);
  z-index: 99;
  height: 180px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  box-shadow: 10px 10px 40px lightgray;
}

.topPanelContainer .topPanelRight .card-bottom .card-bottom-single-card img {
  width: 101px;
  height: 100px;
  object-fit: cover;
}

.topPanelContainer .topPanelRight .card-bottom .card-bottom-single-card {
  width: 101px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-left: 20px;
}
.topPanelContainer .topPanelRight .card-bottom .card-bottom-single-card h4 {
  text-align: center;
  font-size: 10px;
  font-weight: 600;
  line-height: 12px;
  color: black;
  margin: 4px 0;
}

.topPanelContainer .topPanelRight .card-bottom .card-bottom-single-card h5 {
  text-align: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 12px;
  color: black;
  margin: 4px 0;
}

.topPanelContainer
  .topPanelRight
  .card-bottom
  .card-bottom-single-card
  .price-rating
  h6 {
  font-size: 10px;
  line-height: 12px;
}

.topPanelContainer
  .topPanelRight
  .card-bottom
  .card-bottom-single-card
  .price-rating
  img {
  width: 12px;
  height: 12px;
  font-weight: 600;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.topPanelContainer
  .topPanelRight
  .card-bottom
  .card-bottom-single-card
  .price-rating
  h5 {
  font-size: 14px;
  line-height: 17px;
  color: #ff7d05;
  font-weight: bold;
  margin-left: 23px;
}

.flex-even {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.topPanelContainer .topPanelLeft {
  flex: 45%;
}

.topPanelContainer .topPanelLeft h1 {
  color: var(--black);
  font-size: 36px;
  line-height: 54px;
  font-weight: 600;
}

.topPanelContainer .topPanelLeft h2 {
  color: var(--primary);
  font-size: 36px;
  line-height: 54px;
  font-weight: 600;
}

.topPanelContainer h3 {
  color: var(--black);
  font-size: 20px;
  line-height: 30px;
  font-weight: 600;
  margin-top: 40px;
  margin-bottom: 24px;
}
.topPanelContainer li {
  list-style-type: disc;
  margin: 24px 0;
}

.topPanelContainer ul {
  padding-left: 85px;
}


.topPanelContainer .topPanelLeft button{
  display: none;
}

.topPanelContainer .bottompanel-left button{
  display: none;
}
/* bottom panel */



.bottompanel-right {
  background-color: var(--white);
  z-index: 99;
  border-radius: 4px;
  box-shadow: 10px 10px 40px lightgray;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

.topPanelContainer .bottompanel-left h5 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
}

.topPanelContainer .bottompanel-right h3 {
  text-align: center;
  line-height: 31px;
  font-size: 24px;
  font-weight: 600;
  margin-top: 24px;
  margin-bottom: 32px;
}

.topPanelContainer .bottompanel-right h4 {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  line-height: 21px;
  color: #1b1c1e;
}

.topPanelContainer .bottompanel-right h6 {
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  color: #767872;
  margin-bottom: 32px;
}

.topPanelContainer .bottompanel-right .form-container {
  margin-left: 24px;
  margin-top: 40px;
}

.topPanelContainer .bottompanel-right .form-container .form-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 50px;
  margin-bottom: 50px;
  gap: 30px;
}

.topPanelContainer .bottompanel-right .form-container .buttonContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 73px;
  margin-bottom: 24px;
}

.topPanelContainer .bottompanel-right .form-container .buttonContainer button {
  width: 213px;
  height: 52px;
  background-color: var(--primary);
  border: none;
  outline: none;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
}

.flex-checkbox {
  display: flex;
  align-items: center;
}



@media screen and (max-width: 900px) {
  .topPanelContainer {
    flex-direction: column;
    gap: 0;
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 0;
    margin-top: 30px;
  }
  .topPanelContainer .topPanelLeft h1 {
    font-size: 24px;
    line-height: 36px;
  }
  .topPanelContainer .topPanelLeft h2 {
    line-height: 36px;
    font-size: 24px;
    margin-left: 0;
  }
  .topPanelContainer .topPanelLeft h3 {
    line-height: 30px;
    font-size: 20px;
  }
  .topPanelContainer ul {
    padding-left: 34px;
  }

  .topPanelContainer .topPanelLeft button{
    display: block;
    color: var(--primary);
    background-color: transparent;
    border: none;
    padding-left: 20px;
  }

  .topPanelContainer .bottompanel-left button{display: block;
    color: var(--primary);
    background-color: transparent;
    border: none;
    padding-left: 20px}
  li{
    font-size:14px;
    font-weight: 500;
    line-height: 21px;
  }
}
@media screen and (max-width: 900px) {
  .topPanelContainer .topPanelRight img {
    width: 100vw;
    height: 500px;
    object-fit: fill;
    position: relative;
    margin: 0;
    padding: 0;
    margin-bottom: 0px;
  }
  .topPanelContainer .topPanelRight .topPanelCard {
    width: 220px;
    margin-left: 70px;
  }
  .topPanelContainer .topPanelRight .topPanelCard .card-top-second {
    display: none;
  }
  .topPanelContainer .topPanelRight .topPanelCard img {
    width: 16px;
    height: 16px;
    margin: 0px 1px;
  }
  .topPanelContainer .bottompanel-right .form-container {
    margin-left: 0;
    margin-left: 24px;
    margin-top: 0;
  }
  .topPanelContainer .bottompanel-right .form-container .form-top {
    margin-right: 0;
    
  }
  .topPanelContainer .bottompanel-right h3 {
    font-size: 16px;
    line-height: 24px;
  }
  .topPanelContainer .bottompanel-right h3 {
    font-size: 16px;
    line-height: 20px;
  }
  .topPanelContainer .bottompanel-right .form-container .form-top {
    flex-direction: column;
    align-items: flex-start;
  }

  .topPanelContainer .topPanelRight {
    position: relative;
  }
  .topPanelContainer .topPanelRight .topPanelCard {
    position: absolute;
    top: 100px;
    left: 129px;
    border-radius: 0px;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    -ms-border-radius: 0px;
    -o-border-radius: 0px;
    box-shadow: 10px 10px 40px lightgray;
    z-index: 0;
  }
  .topPanelContainer .topPanelRight .card-bottom {
     width: 192px;
     height: 70px;
     background-color: #fff;
     position: absolute;
     top: 70%;
     border-radius:0  ;
     -webkit-border-radius:0  ;
     -moz-border-radius:0  ;
     -ms-border-radius:0  ;
     -o-border-radius:0  ;
}
  .topPanelContainer .topPanelRight .card-bottom .card-bottom-single-card {
    width: 50px;
    margin-left: 10px;
  }
  .topPanelContainer .topPanelRight .card-bottom .card-bottom-single-card img {
    width: 50px;
    height: 30px;
    object-fit: cover;
  }
  .topPanelContainer
    .topPanelRight
    .card-bottom
    .card-bottom-single-card
    .flex-center {
    display: none;
  }
  .card-bottom-single-card .flex-center img,
  h5 {
    display: none;
  }
  .topPanelContainer .bottompanel-right .form-container .form-top {
    margin-bottom: 0px;
  }
  .topPanelContainer .bottompanel-right .form-container .buttonContainer {
    margin-bottom: 20px;
  }
}
